<template>
  <Center>
      <form class=".login-container" @submit.prevent="handleSubmit()">
          <div class="form-item">
              <div class="input"><label>账号：</label><input v-model="userInfo.loginId" type="text" @input="validateEmpty('loginId','账号不能为空')" /></div>
              <FoemError :msg="error.loginId"/>
          </div>
          <div class="form-item">
              <div class="input"><label>密码：</label><input v-model="userInfo.loginPwd" type="password" @input="validateEmpty('loginPwd','密码不能为空')" /></div>
              <FoemError :msg="error.loginPwd"/>
          </div>
          <div class="form-item">
              <div class="input"><label></label><button>{{$store.state.loginUser.isLoading?"isLoading...":"登录"}}</button></div>
          </div>
      </form>
  </Center>
</template>

<script>
import Center from '../components/Center'
import FoemError from '../components/FoemError'
import {reg} from '../services/userService'
export default {
    components:{
        Center,
        FoemError,
    },
    data(){
        return {
            //获取注册的信息
            userInfo:{
                //账号
                loginId:"",
                //密码
                loginPwd:"",
            },
            //错误消息
            error:{
                //账号
                loginId:"",
                //密码
                loginPwd:"",
            }
        }
    },
    methods:{
        //非空验证
        validateEmpty(field,msg){
            //判断userInfo是否有值
            if(this.userInfo[field]){
                //如果有值错误消息为空
                this.error[field] = "";
                return true;
            }else {
                //如果没值传入指定错误消息
                this.error[field] = msg;;
                return false;
            }
        },
        //提交表单
        async handleSubmit(){
            if (this.$store.state.loginUser.isLoading){
                return;
            };
            //提交表单前，判断数据是否正确
            var validate1 = this.validateEmpty('loginId','账号不能为空');
            var validate2 = this.validateEmpty('loginPwd','密码不能为空');
            if(validate1 & validate2){
                //提交数据
               var result = await this.$store.dispatch("loginUser/login",this.userInfo);
               console.log(result);
            };
            if(result){
                //登录成功进行跳转
                this.$router.push({name:"Home"});
            }else{
                //登录失败
                this.error.loginId = "账号/密码不正确";
            }
        },
    },
}
</script>

<style scopde>
.login-container{
    width: 400px;
}

.form-item .input{
    display: flex;
}
.form-item label{
    width: 100px;
    display: inline-block;
    text-align: right;
    line-height: 41px;
}

.form-item input,
.form-item button{
    flex-grow: 1;
}
</style>