<template>
  <div class="error" :class="{'txt-center':isCenter,}" :style="{visibility:msg?'visible':'hidden'}">{{msg}}</div>
</template>

<script>
export default {
    //判断是否居中
    props:{
        //判断是否居中
        isCenter:{
            default:false,
            type:Boolean,
        },
        //显示数据
         msg:{
        default:"",
        type:String,
        },
    },
}
</script>

<style scoped>
.error.txt-center{
    text-align: center;
}

.error{
    font-size: 14px;
    color: #a44242;
    padding-left: 100px;
    height: 30px;
    line-height: 30px;
}
</style>